@import url("https://fonts.googleapis.com/css?family=Bree+Serif");
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Bree Serif";
}
body{
    height: 100vh;
    width: 100vw;
    background-color: rgb(150, 126, 148);
    display: flex;
    justify-content: center;
    align-items: center;
  
}
.form{
width:100%;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
border: none;
border-radius:5px;

}
.progressbar{
    width: 500px;
    height: 10px;
    border:none;
    border-radius: 30px;
    background-color:#fff;
    margin-bottom: 50px;

}

.form-container{
    width: 450px;
    height: 450px;
    background: #fff;
    border: none;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 25px 80px rgba(0, 0, 0, 0.5);
}
 .header{
margin: 30px 0px 30px ;
 font-size:18px;
}
.sign-up-container{
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
}
.sign-up-container input{
    width: 400px;
    margin: 10px;
    padding: 7px;
    border: 1px solid rgb(211, 120, 225);
    border-radius: 10px;
    outline: none;
    font-size: 15px;
    cursor: pointer;
}
.footer button{
    width: 70px;
    height: 30px;
    margin:25px 15px;
    cursor: pointer;
    border: none;
    font-size:15px;
    border-radius: 50%;
    background-color: rgb(207, 212, 224);
    
}
.footer button:hover{
  
    box-shadow: rgba(42, 42, 68, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;

}
.personal-info-container{
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;

}
.personal-info-container input{
    width: 400px;
    margin: 10px;
    padding: 7px;
    border: 1px solid rgb(211, 120, 225);
    border-radius: 10px;
    outline: none;
    font-size: 15px;
    cursor: pointer;
}
.other-info-container{
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
}
.other-info-container input{
    width: 400px;
    margin: 10px;
    padding: 7px;
    border: 1px solid rgb(211, 120, 225);
    border-radius: 10px;
    outline: none;
    font-size: 15px;
    cursor: pointer;
}
.progress-bar{
    width: 33.3%;
    height: 100%;
    background-color: rgb(95, 245, 8);
    border-radius:30px ;
}